import { baseRequest } from '../base';
// api for Invoices 

const getCustomerCategories = async (data, signal) => {
  try {
    return await baseRequest('api/v1/sales/customers/category', 'POST', data, signal);
  } catch (e) {
    alert(e)
  }
};


